import { useEffect } from 'react';

export default function Main( {data} ){

useEffect(()=>{
const elMenu = document.querySelectorAll('.menu li');
let k=0;
function menu(){
    for(let i=0; i<elMenu.length; i++){
    elMenu[i].addEventListener("click",function(){
        elMenu[k].classList.remove('active');
        elMenu[i].classList.add('active');
        k=i;
    })
    }
}
menu();

const elPop = document.querySelectorAll('.popup');
const elBtn = document.querySelectorAll('.circle');
function pop(){
    for(let i=0; i<elBtn.length; i++){
        elBtn[i].addEventListener("click",function(){
        elPop[i].classList.add('active');
    })
        elPop[i].addEventListener("click",function(){
        elPop[i].classList.remove('active');
    })
    }
}
pop();
})

    return(
        <>
        <li>
            <img src={data.img} alt=""/>
            <div className="hover">
                <h3>{data.tit}</h3>
                <span>{data.sub}</span>
                <span class="line"/>
                <div class="circle">→</div>
            </div>
        </li>
        <div className="popup">
            <img src={data.img} alt=""/>
        </div>
        </>
    )
}