import './App.css';
import Main from './comp/Main';
import photo1 from './img/img1.jpg';
import photo2 from './img/img2.jpg';
import photo3 from './img/img3.jpg';
import photo4 from './img/img4.jpg';
import photo5 from './img/img5.jpg';
import photo6 from './img/img6.jpg';
import photo7 from './img/img7.jpg';
import photo8 from './img/img8.jpg';
import photo9 from './img/img9.jpg';
import photo10 from './img/img10.jpg';
import photo11 from './img/img11.jpg';
import photo12 from './img/img12.jpg';
import { BrowserRouter, Route, Switch, Link } from 'react-router-dom';
import Covid from './comp/Covid';
import Map from './comp/Map';

function App() {

  const API_KEY = "b7d0c5bc7ada2e64748be79d7b952fa6";
    
    function onGeoOk(position){
        const lat =position.coords.latitude;
        const lng =position.coords.longitude;
        const url = `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lng}&appid=${API_KEY}&units=metric`;
        fetch(url)
        .then((response) => response.json())
        .then((data) => {
            const weather = document.querySelector(".weather");
            const temp = document.querySelector(".temp");
            const city = document.querySelector(".city");
            city.innerText = data.name;
            weather.innerText = `${data.weather[0].main}`;
            temp.innerText = ` ${Math.floor(data.main.temp)}℃`;
        });
    };
    function onGeoError(){
        alert("can't find you")
    };
    navigator.geolocation.getCurrentPosition(onGeoOk,onGeoError);

  const imgSrc=[
    {img:photo1,tit:"인테리어",sub:"Hello~"},
    {img:photo2,tit:"잡지",sub:"Nice Photo~"},
    {img:photo3,tit:"잡지",sub:"Good :)"},
    {img:photo4,tit:"인쇄디자인",sub:"Hi there!"},
    {img:photo5,tit:"잡지",sub:"How are you?"},
    {img:photo6,tit:"스터디 이미지",sub:"안녕하세요~"},
    {img:photo7,tit:"데스트 이미지",sub:"예쁜 사진입니다."},
    {img:photo8,tit:"전시회",sub:"분위기가 좋아요~"},
    {img:photo9,tit:"전시회",sub:"박선후 갤러리 페이지"},
    {img:photo10,tit:"루프탑",sub:"좋은 하루 되세요!"},
    {img:photo11,tit:"페스티벌",sub:"오늘도 행복하게~"},
    {img:photo12,tit:"요리",sub:"힘차게 지냅시다!"}
  ];
  
  
  
  return (
    <BrowserRouter>
    <header className="header">
      <div className="logo">
        SUN space
        <p className="subtit">- react page -</p>
      </div>
      <ul class="menu">
      <Link to="/react/"><li className="active">HOME</li></Link>
      <Link to="/covid"><li>COVID-19</li></Link>
      </ul>
      <div class="current_weather">
        <p>Today's Weather</p>
        <div class="city"></div>
        <div class="wea-tmp">
            <span class="weather"></span>
            <span class="temp"> </span>
        </div>
      </div>
    </header>
      <Switch>
    <main>
        <Route exact path="/react/">
          <ul>
            {
              imgSrc.map( (src,k) => (
                <Main data={src} key={k}/>
              ) )
            }
          </ul>
        </Route>
        <Route path="/covid"><Covid/></Route>
    </main>
      </Switch>
    </BrowserRouter>
  );
}

export default App;
